import { Component }       from '@angular/core';
import{HttpClient,HttpHeaders}from'@angular/common/http';

@Component({
  selector: 'app-child',
  templateUrl: './login.html'
})

export class Login {
  username: string = '';
  password: string = '';
  token: string;
  message: string;
  errorMessage: string ='';

  constructor(private http: HttpClient) {
  }

  login(){
    leturl="http://localhost:1337/auth";

    //Thisfreeonlineservicereceivespostsubmissions.
    this.http.post(url,{
      username:this.username,
      password:this.password,
    })
      .subscribe(
        //Dataisreceivedfromthepostrequest.
        (data)=>{
          //Inspectthedatatoknowhowtoparseit.
          console.log(JSON.stringify(data));

          if(data["token"]!=null){
            this.token=data["token"];
            sessionStorage.setItem('auth_token',data["token"]);
            this.message="Theuserhasbeenloggedin.";
            window.location.href = 'http://localhost:4200/main';
          }
        },
        //Anerroroccurred.Dataisnotreceived.
        error=>{
          this.errorMessage = 'Error: wrong username or password.';
        });
  }

}
